.bloginfo-container{
    min-height: 120px;
    padding: 12px;
    margin-bottom: 10px;
    box-shadow: 2px 2px 8px rgba(0,0,0,.2);
    border-color: #d6d6d6;
}
.bloginfo-header{
    padding: 15px 0 10px 0;
    margin-bottom: 12px;
    text-align: center;
    border-bottom: 1px solid #ccc;
}
.bloginfo-header>h2{
    margin-bottom: 5px;
    font-family: '微软雅黑';
}
.bloginfo-text>span{
    margin: 0 10px;
    font-family: '宋体';
}
.bloginfo-content{
    padding: 12px;
    background-color:#F5F8FD;
}
.bloginfo-content>p{
    line-height: 30px;
    text-indent: 18px;
    font-size: 16px;
    font-family: '微软雅黑';
}



