.rightBar-container{
    width: 35%;
    display: inline-block;
    vertical-align: top;
    margin-left: 12px;
}
.blogClass-wrapper,.dailyProverbs-wrapper{
    min-height: 200px;
    padding: 12px;
    border: solid 1px #ccc;
    border-top: 4px solid dodgerblue;
    margin-bottom: 12px;
}
.blogClass-wrapper>h3{
    margin: 12px 12px;
    font-family: "微软雅黑";
}
.dailyProverbs-wrapper>h3{
     margin:12px 0;
     font-family: "微软雅黑";
}
.dailyProverbs-wrapper>ul{
    line-height: 24px;
    margin-left: 8px;
    font-family: '微软雅黑';
    font-size: 14px;
}
.maxim_list{
    margin-bottom: 8px;
   
}
.maxim_list>span{
    padding:2px 8px;
    margin-right: 5px;
    border-radius: 100%;
    background: lightskyblue;
    font-size: 12px;
   
}
@media screen and (max-width:1330px){
    .rightBar-container{
        width:calc(100%);
        margin-left: 0px;
    }
    .blogClass-wrapper,.dailyProverbs-wrapper{
        border-left: none;
        border-right: none;
    }
}







