.header-container{
    width:100%;
    height: 80px;
    line-height: 80px;
    margin-bottom: 15px;
    border-bottom: 1px solid #efefef;
    box-shadow: 0 1px 1px rgba(0,0,0,.1);
    background-color: #fff;
    opacity: 1;
    z-index: 10;
}
.left-wrapper{
    width: 200px;
}
.nav-wrapper{
    width: 500px;
    float: right;
}
.nav-wrapper>ul{
    display: flex;
}
.nav-wrapper>ul>li{
    flex: 1;
    text-align: center;     
}
.nav-wrapper>ul>li>a{
    text-decoration: none;
    font-family: '微软雅黑';
    font-size: 18px;
    color: black;
}

@media screen and (max-width:1330px){
    .nav-wrapper{
        width:calc(100% - 4px);
        float: none;
    }
}




