.banner-container{
    width: 60%;
    height: 200px;
    margin:18px 0 18px 18px;
    display: inline-block;
    position: relative;
    overflow: hidden;
}

.banner-wrapper{
    width:calc(100% * 3);
    height: 100%;
}

.banner-box{
    width:calc(100%/3);
    float: left;
    height: 100%;
}

.banner-img{
    width: 100%;
    height: 100%;
    background-size: cover;
}

.dot-warpper{
    position: absolute;
    left:10px;
    top:175px;
}
.dot{
    padding: 2px 8px;
    margin: 5px;
    border-radius: 60%;
    background-color: orange;
}
.active-dot{
    background-color: green;
}

.info-wrapper{
    width: 36%;
    margin-left: 14px;
    display: inline-block;
    vertical-align: top;
}

.info-left,.info-right{
    display: inline-block;
    width: 49%;
    vertical-align: center;
    vertical-align: top;
}

.info-left>h3{
    margin-left: 18px;
    margin-top:5px;
    font-family: "微软雅黑";
    font-size:24px;
    color:#ccc;
}

.info-right>h3{
    margin-top:12px;
    font-family: "微软雅黑";
    font-size: 24px;
    border-left: #ccc 8px solid;
}

.info-right>p{
    line-height: 25px;
    font-size: 12px;
    font-family: "微软雅黑";
    text-indent: 16px;
}

.image-box>img{
   border-radius: 100%;
   margin-top: 25px;
   margin-left: 18px;
}


@media screen and (max-width:1330px){
   .info-wrapper{
       display: none;
   }
   .banner-container{
       width: 100%;
       margin:18px 0;
   }
}







