.leftBar-container{
    width: 60%;
    display: inline-block;
    margin-left: 18px;
}
.leftBar-wrapper{
    min-height: 80px;
    padding: 12px;
    margin-bottom: 10px;
    box-shadow: 2px 2px 8px rgba(0,0,0,.2);
    border-color: #d6d6d6;
}
.pic-wrapper,.text-info{
    display: inline-block;
}
.pic-wrapper{
    width: 160px;
}

.blogPicture{
    border-radius: 4px;
    width: 100%;
    height:150px;
    background-size: cover;
}
.text-info{
    width:76%;
    vertical-align: top;
    padding: 10px;

}
@media screen and (max-width:1330px){
    .leftBar-container{
        width:calc(100% - 4px);
        margin-left:0px;
    }

    .text-info{
        width:calc(100% - 4px);
        display: block;
        margin-left: 0;
    }
}
.text-info>h3{
    margin:12px 0;
    font-family: '微软雅黑';
}
.text-info>p{
    margin-top:8px;
    line-height: 18px;
    text-indent:16px;
    font-family: '微软雅黑';
    font-size: 14px;
    color: #999;
    word-break:break-all;
    text-overflow:ellipsis;
}
.publishinfo-wrapper{
    margin-top: 12px;
}
.publishinfo-wrapper>a{
    padding: 5px 10px;
    background-color: cornflowerblue;
    border-radius: 5px;
    text-decoration: none;
    color: white;
    font-family: '微软雅黑';
}
.publishinfo-wrapper>span{
    margin-left: 10px;
    color:#181818;
    font-family: '宋体';
}


